import React, { useState, useRef, useContext } from "react";
import styled from "styled-components";
import { useAuthState } from "react-firebase-hooks/auth";
import { auth, db } from "../firebase";
import { useRouter } from "next/router";
import { Avatar, IconButton } from "@mui/material";
import MoreVertIcon from "@mui/icons-material/MoreVert";
import AttachFileIcon from "@mui/icons-material/AttachFile";
import InsertEmoticonIcon from "@mui/icons-material/InsertEmoticon";
import MicIcon from "@mui/icons-material/Mic";
import SendIcon from "@mui/icons-material/Send";
import {
  collection,
  query,
  where,
  orderBy,
  addDoc,
  serverTimestamp,
  doc,
  updateDoc,
  setDoc,
} from "firebase/firestore";
import { useCollection } from "react-firebase-hooks/firestore";
import Message from "./Message";
import TimeAgo from "timeago-react";
import Menu from "@mui/material/Menu";
import MenuItem from "@mui/material/MenuItem";
import Popover from "@mui/material/Popover";
import { DarkModeContext } from "./DarkModeProvider";

function ChatScreen({ chat, messages }) {
  const [user] = useAuthState(auth);
  const [input, setInput] = useState("");
  const endOfMessagesRef = useRef(null);
  const router = useRouter();
  const [anchorEl, setAnchorEl] = useState(null);
  const [emojiAnchorEl, setEmojiAnchorEl] = useState(null);

  // Get dark mode context
  const darkModeContext = useContext(DarkModeContext);
  const { darkMode } = darkModeContext || { darkMode: false };

  // Handle self-chat case
  const recipientEmail = chat.users.find((userEmail) => userEmail !== user?.email) || user?.email;
  
  const recipientQuery = query(
    collection(db, "users"),
    where("email", "==", recipientEmail)
  );

  const [recipientSnapshot] = useCollection(recipientQuery);
  const recipient = recipientSnapshot?.docs?.[0]?.data();

  const messagesQuery = query(
    collection(db, "chats", router.query.id, "messages"),
    orderBy("timestamp", "asc")
  );

  const [messagesSnapshot] = useCollection(messagesQuery);

  // Check if this is a self-chat
  const isSelfChat = chat.users.length === 2 && chat.users[0] === chat.users[1];

  // Get blocked users
  const userDocRef = user ? doc(db, "users", user.uid) : null;
  const [userDocSnapshot] = useCollection(
    userDocRef
      ? query(collection(db, "users"), where("__name__", "==", user.uid))
      : null
  );
  const currentUserData = userDocSnapshot?.docs?.[0]?.data();
  const blockedUsers = currentUserData?.blockedUsers || [];

  const isRecipientBlocked = !isSelfChat && blockedUsers.includes(recipientEmail);

  const handleMenuOpen = (event) => {
    setAnchorEl(event.currentTarget);
  };

  const handleMenuClose = () => {
    setAnchorEl(null);
  };

  const handleEmojiClick = (event) => {
    setEmojiAnchorEl(event.currentTarget);
  };

  const handleEmojiClose = () => {
    setEmojiAnchorEl(null);
  };

  const onEmojiClick = (emojiObject) => {
    setInput((prevInput) => prevInput + emojiObject.emoji);
  };

  const showMessages = () => {
    if (messagesSnapshot) {
      return messagesSnapshot.docs.map((message) => (
        <Message
          key={message.id}
          user={message.data().user}
          message={{
            ...message.data(),
            timestamp: message.data().timestamp?.toDate().getTime(),
          }}
        />
      ));
    } else {
      return JSON.parse(messages).map((message) => (
        <Message key={message.id} user={message.user} message={message} />
      ));
    }
  };

  const scrollToBottom = () => {
    endOfMessagesRef.current?.scrollIntoView({
      behavior: "smooth",
      block: "start",
    });
  };

  const sendMessage = async (e) => {
    e.preventDefault();

    if (!input.trim()) return;

    if (isRecipientBlocked) {
      alert("You have blocked this user. Unblock them to send messages.");
      return;
    }

    try {
      // Update user's last seen
      await setDoc(
        doc(db, "users", user.uid),
        {
          lastSeen: serverTimestamp(),
        },
        { merge: true }
      );

      // Add message to chat
      await addDoc(collection(db, "chats", router.query.id, "messages"), {
        timestamp: serverTimestamp(),
        message: input,
        user: user.email,
        photoURL: user.photoURL,
      });

      setInput("");
      scrollToBottom();
    } catch (error) {
      console.error("Error sending message:", error);
      alert("Failed to send message");
    }
  };

  return (
    <Container darkMode={darkMode}>
      <Header darkMode={darkMode}>
        {recipient ? (
          <Avatar src={recipient.photoURL} />
        ) : (
          <Avatar>{recipientEmail?.[0]?.toUpperCase()}</Avatar>
        )}

        <HeaderInformation>
          <h3>{isSelfChat ? `${recipientEmail} (You)` : recipientEmail}</h3>
          {recipient && !isSelfChat ? (
            <p>
              Last active:{" "}
              {recipient.lastSeen?.toDate() ? (
                <TimeAgo datetime={recipient.lastSeen.toDate()} />
              ) : (
                "Unavailable"
              )}
            </p>
          ) : isSelfChat ? (
            <p>Message yourself</p>
          ) : (
            <p>Loading...</p>
          )}
        </HeaderInformation>

        <HeaderIcons>
          <IconButton>
            <AttachFileIcon />
          </IconButton>
          <IconButton onClick={handleMenuOpen}>
            <MoreVertIcon />
          </IconButton>
        </HeaderIcons>
      </Header>

      <MessageContainer darkMode={darkMode}>
        {showMessages()}
        <EndOfMessage ref={endOfMessagesRef} />
      </MessageContainer>

      {isRecipientBlocked && (
        <BlockedWarning darkMode={darkMode}>
          You have blocked this user. Unblock them to send messages.
        </BlockedWarning>
      )}

      <InputContainer darkMode={darkMode}>
        <IconButton onClick={handleEmojiClick}>
          <InsertEmoticonIcon />
        </IconButton>
        <Input
          darkMode={darkMode}
          value={input}
          onChange={(e) => setInput(e.target.value)}
          placeholder="Type a message"
          disabled={isRecipientBlocked}
        />
        <IconButton disabled={!input.trim() || isRecipientBlocked} onClick={sendMessage}>
          <SendIcon />
        </IconButton>
        <IconButton>
          <MicIcon />
        </IconButton>
      </InputContainer>

      <Menu anchorEl={anchorEl} open={Boolean(anchorEl)} onClose={handleMenuClose}>
        <MenuItem onClick={handleMenuClose}>Contact Info</MenuItem>
        <MenuItem onClick={handleMenuClose}>Select Messages</MenuItem>
        <MenuItem onClick={handleMenuClose}>Mute Notifications</MenuItem>
        <MenuItem onClick={handleMenuClose}>Delete Chat</MenuItem>
      </Menu>

      <Popover
        open={Boolean(emojiAnchorEl)}
        anchorEl={emojiAnchorEl}
        onClose={handleEmojiClose}
        anchorOrigin={{
          vertical: "top",
          horizontal: "left",
        }}
        transformOrigin={{
          vertical: "bottom",
          horizontal: "left",
        }}
      >
        <EmojiPicker onEmojiClick={onEmojiClick} />
      </Popover>
    </Container>
  );
}

export default ChatScreen;

const Container = styled.div`
  display: flex;
  flex-direction: column;
  height: 100vh;
  background-color: ${(props) => (props.darkMode ? "#1e1e1e" : "white")};
`;

const Header = styled.div`
  position: sticky;
  background-color: ${(props) => (props.darkMode ? "#2a2a2a" : "white")};
  z-index: 100;
  top: 0;
  display: flex;
  padding: 11px;
  height: 80px;
  align-items: center;
  border-bottom: 1px solid ${(props) => (props.darkMode ? "#333" : "whitesmoke")};
`;

const HeaderInformation = styled.div`
  margin-left: 15px;
  flex: 1;

  > h3 {
    margin: 0;
    margin-bottom: 3px;
  }

  > p {
    margin: 0;
    font-size: 14px;
    color: gray;
  }
`;

const HeaderIcons = styled.div``;

const MessageContainer = styled.div`
  padding: 30px;
  background-color: ${(props) => (props.darkMode ? "#0d1117" : "#e5ded8")};
  min-height: 90vh;
  flex: 1;
  overflow-y: scroll;

  ::-webkit-scrollbar {
    display: none;
  }

  -ms-overflow-style: none;
  scrollbar-width: none;
`;

const EndOfMessage = styled.div`
  margin-bottom: 50px;
`;

const InputContainer = styled.form`
  display: flex;
  align-items: center;
  padding: 10px;
  position: sticky;
  bottom: 0;
  background-color: ${(props) => (props.darkMode ? "#2a2a2a" : "white")};
  border-top: 1px solid ${(props) => (props.darkMode ? "#333" : "whitesmoke")};
  z-index: 100;
`;

const Input = styled.input`
  flex: 1;
  outline: 0;
  border: none;
  border-radius: 10px;
  background-color: ${(props) => (props.darkMode ? "#1e1e1e" : "whitesmoke")};
  color: ${(props) => (props.darkMode ? "#e0e0e0" : "black")};
  padding: 15px;
  margin-left: 15px;
  margin-right: 15px;

  ::placeholder {
    color: ${(props) => (props.darkMode ? "#888" : "#999")};
  }

  :disabled {
    opacity: 0.5;
    cursor: not-allowed;
  }
`;

const BlockedWarning = styled.div`
  background-color: #fff3cd;
  color: #856404;
  padding: 10px;
  text-align: center;
  border-top: 1px solid #ffeeba;
`;